.BigName{
  color: #FFFFFF;
  text-shadow: #474747 3px 5px 2px;
  color: #000000;
  font-size: 5em;
  transition-duration: .5s;
}

.headBand{
  width: 100%;
  height: 10vh;
  align-items: center;
  justify-content: center;
  text-align: center;
  display: flex;
  background: #ce7f7f;
}

.ProfilePicture{
  transition : transform 0.5s;
}

.ProfilePicture:hover {
  transition-duration: .5s;
  transform: scale(200%);
  cursor: pointer;
}

.ProfilePicture:hover ~ .BigName{
  transition-duration: .5s;
  font-size: 6em;
  margin-left:10em;
}

body{
  background: #474747;
}

